import Web3 from "web3";

const rpcUrls = {
  celo: [
    "https://1rpc.io/celo",
    "https://rpc.ankr.com/celo",
    "https://forno.celo.org",
    "https://celo.api.onfinality.io/public",
    "https://api.tatum.io/v3/blockchain/node/celo-mainnet",
  ],
  matic:[
    "https://polygon-mainnet.public.blastapi.io",
    "https://api.tatum.io/v3/blockchain/node/polygon-mainnet",
    "https://public.stackup.sh/api/v1/node/polygon-mainnet",
    "https://polygon-bor-rpc.publicnode.com",
    "https://polygon.rpc.blxrbdn.com",
    "https://polygon-pokt.nodies.app",
    "https://rpc-mainnet.matic.quiknode.pro",
    "https://polygon-rpc.com",
  ]
};

// 检查哪个 RPC URL 在获取区块高度时响应最快
async function getFastestRpcUrl(urls) {
  const responseTimes = await Promise.all(
    urls.map(async (url) => {
      const web3 = new Web3(url);
      const startTime = performance.now();
      try {
        await web3.eth.getBlockNumber();
        const endTime = performance.now();
        return { url, time: endTime - startTime };
      } catch (error) {
        return { url, time: Infinity }; // 如果请求失败，设置响应时间为无限大
      }
    })
  );

  // 选择响应时间最短的 URL
  const fastestUrl = responseTimes.sort((a, b) => a.time - b.time)[0].url;
  if (!fastestUrl) {
    throw new Error("无法连接到任何提供的 RPC URL");
  }
  console.log(fastestUrl)
  return fastestUrl;
}

// 初始化所有 Web3 实例的函数
async function initializeWeb3Instances() {
  const web3 = new Web3(window.ethereum);
  const web3HECOT = new Web3("https://http-testnet.hecochain.com");
  const web3BSCT = new Web3("https://data-seed-prebsc-1-s1.binance.org:8545");
  const web3ETH = new Web3(
    "https://mainnet.infura.io/v3/9aa3d95b3bc440fa88ea12eaa4456161"
  );
  const web3HECO = new Web3("https://http-mainnet-node.huobichain.com/");
  const web3BSC = new Web3("https://bsc-dataseed.binance.org/");
  const web3OEC = new Web3("https://exchainrpc.okex.org");
  const web3OECT = new Web3("https://exchaintestrpc.okex.org");
  const fastestMaticUrl = await getFastestRpcUrl(rpcUrls.matic);
  const web3MATIC = new Web3(fastestMaticUrl);
  const fastestCeloUrl = await getFastestRpcUrl(rpcUrls.celo);
  const web3CELO = new Web3(fastestCeloUrl);
  const web3FIL = new Web3("https://filfox.info/rpc/v1");

  return {
    web3,
    web3HECOT,
    web3BSCT,
    web3ETH,
    web3HECO,
    web3BSC,
    web3OEC,
    web3OECT,
    web3MATIC,
    web3CELO,
    web3FIL,
  };
}

// 创建一个 promise，等待 Web3 实例初始化完成
const web3morePromise = initializeWeb3Instances();

// 导出一个对象，该对象在 promise 完成后解析为初始化后的 Web3 实例
const web3more = {};
web3morePromise.then((instances) => {
  Object.assign(web3more, instances);
});

export { web3morePromise };
export default web3more;
